$(document).ready(function(){
    // wow init
    new WOW().init();

    //preloader
    let preload = $('#preload');
    let body = $('body');

    setTimeout(() => {
        preload.addClass('hide');
    }, 1200);

    setTimeout(() => {
        body.removeClass('overflow-h');
    }, 2200)

    // icons parallax effect
    let scene = document.getElementById('scene');
    let parallaxInstance = new Parallax(scene);

    // header sroll parallax-animate
    $('.parallax-window-header').parallax({imageSrc: '../img/header_background.png'});

    //navigation
    let btnToUp = $('#button-up');
    let menu = $('#menu');
    
    $(window).scroll(() => {
        if ($(window).scrollTop() > 800) {
            btnToUp.addClass('show');
        } else {
            btnToUp.removeClass('show');
        }
      });

    linkHref(btnToUp);
    linkHref(menu);
    
})