$(document).ready(function(){

    let menu = $('#menu');
    menu.addClass('animated bounceInDown delay-2s');

    let social = $('.about-me__item')
    social.addClass('animated fadeInUp');

    let iconDev = $('.header__icons-group');
    setTimeout(() => {
        iconDev.removeClass('hide-icons');
    }, 2200)

    let skills = $('#profile__skills');  
    skills.addClass('wow slideInLeft')

    let exp = $('#profile__expirience');  
    exp.addClass('wow slideInRight')

    let edu = $('#profile__education');  
    edu.addClass('wow slideInLeft');

    let projectItem = $('.portfolio__project-item');
    projectItem.addClass('wow fadeIn');

    let portfolio = $('#portfolio');
    portfolio.on('click', 'a', function(event){
        event.preventDefault();
        let id = $(this).attr('href');
        let item = $(this).closest('.portfolio__project-item');
        item.addClass('moveup');

        setTimeout(() => {
            window.open(id, '_blank');
            item.removeClass('moveup');
        }, 600)
    })
})