$(document).ready(() => {
    function createCircle() {
        let circleBlock = document.createElement('div');
        circleBlock.style.zIndex = "102";
        circleBlock.classList.add('circle-wave');
        document.body.appendChild(circleBlock);
        return circleBlock;
      }
      
      function getCirclePositon(elem) {
        elem.style.left = event.clientX + window.pageXOffset + "px";
        elem.style.top = event.clientY + window.pageYOffset + "px";
      }
      
      function deleteCircle(elem) {
        setTimeout(function() {
          document.body.removeChild(elem)
        }, 450)
      }
      
      window.addEventListener('click', function(event) {
        let circleBlock = createCircle();
        getCirclePositon(circleBlock);
        deleteCircle(circleBlock);
      })
})